@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
    color: white;
    font-size: 1.1rem;
}

body {
    background-color: #20242D;
}

@media (min-width: 1024px) {
    :root {
        font-size: 1.3rem;
    }
}

#overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.6);
    /*pointer-events: none;*/
    /*display: block;*/
}

#sideNav {
    box-shadow: 4rem 0 rgba(0, 0, 0, 0.6)
}

.navToggle {
    right: 0;
    transition: right 0.5s ease-out;
}

#sideNavTop {
    background-color: #313746;
}

#sideNav nav {
    background-color: #1e2430;
}