@media (max-width: 1023px) {
    nav ul li a::before {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        height: 0;
        width: 100%;
        border-bottom: 3px solid white;
    }
    
    nav ul li a:hover::before {
        border-bottom: 3px solid rgb(59 130 246);
    }
    
    nav ul li a::after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        height: 0;
        width: 100%;
        border-bottom: 1px solid white;
    }

    nav ul li a:hover::after {
        transition: border 0.05s ease-in;
        border-bottom: 3px solid rgb(59 130 246);
    }

    nav ul li a{
        position: relative;
    }
}

