@media (min-width: 1024px) {
    nav ul li a::before {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        height: 0;
        width: 100%;
        border-bottom: 2px solid white;
    }
    
    nav ul li a:hover::before {
        border-bottom: 2px solid rgb(59 130 246);
    }
    
    nav ul li a:hover::after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        height: 0;
        width: 0;
        border-bottom: 2px solid rgb(59 130 246);
        animation: grow 0.2s ease-out;
        animation-fill-mode: forwards;
    }
    
    @keyframes grow {
        100% {
            width: 100%;
        }
    }
    
    nav ul li a{
        position: relative;
    }
}

